import {Pipe, PipeTransform} from "@angular/core";
@Pipe({name: 'productCategory'})
export class ProductCategoryPipe implements PipeTransform {
  transform(str: String) {
    let transStr = "";
    switch (str) {
      case '10001':
        transStr = "";
        break;
      case '10002':
        transStr = "";
        break;
      case '10003':
        transStr = "";
        break;
      case '10004':
        transStr = "";
        break;
      case '10005':
        transStr = "";
        break;
      case '10006':
        transStr = "";
        break;
      case '10007':
        transStr = "";
        break;
    }
    return transStr;
  }
}



